.index-page{width:100%;position:absolute;top:0;bottom:0;left:0;background-color:#fff;background-image:url(./images/bg@2x.png);background-repeat:no-repeat;background-size:cover;background-position:top right}.index-page header{background-color:transparent}.index-page header nav{padding-right:0}.index-page header .header-container{max-width:1200px;margin:0 auto;padding:0 24px}.index-page header .logo{width:200px;text-align:left}.index-page main{position:relative;max-width:1200px;margin:0 auto;padding:100px 24px;height:100%}.index-page .introduce{display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-flex-direction:column;-ms-flex-direction:column;flex-direction:column;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;width:42%;height:100%}.index-page .introduce .title{font-size:64px;font-weight:bold;line-height:1}.index-page .introduce .title span{color:#00bc70}.index-page .introduce .description{margin-top:30px;font-size:20px;line-height:1.5}.index-page .introduce .navigation{margin-top:100px;margin-bottom:60px;text-align:left}.index-page .introduce .navigation button{float:left;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;-webkit-align-items:center;-ms-flex-align:center;align-items:center;min-height:50px;background-color:#00bc70;border:0;color:#fff;font-size:18px;border-radius:25px;outline:none;margin-bottom:10px;padding:0 30px}.index-page .introduce .navigation button:not(:last-child){margin-right:15px}.index-page .introduce .navigation button:hover{background:#00a864}.index-page .introduce .navigation button.ghost{border:1px solid #00bc70;color:#00bc70;background-color:#fff}.index-page .introduce .navigation button.ghost:hover{border-color:#00a864;color:#00a864}.index-page .banner{float:right;width:55%;height:100%;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-align-items:center;-ms-flex-align:center;align-items:center}.index-page .banner img{margin-top:80px;width:100%;-webkit-animation:banner-move 60s infinite;animation:banner-move 60s infinite}.btn-try{min-width:auto !important;line-height:1}@-webkit-keyframes banner-move{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}20%{-webkit-transform:translate3d(-30px, -30px, 0);transform:translate3d(-30px, -30px, 0)}40%{-webkit-transform:translate3d(30px, -30px, 0);transform:translate3d(30px, -30px, 0)}60%{-webkit-transform:translate3d(-30px, 30px, 0);transform:translate3d(-30px, 30px, 0)}80%{-webkit-transform:translate3d(30px, 30px, 0);transform:translate3d(30px, 30px, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@keyframes banner-move{0%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}20%{-webkit-transform:translate3d(-30px, -30px, 0);transform:translate3d(-30px, -30px, 0)}40%{-webkit-transform:translate3d(30px, -30px, 0);transform:translate3d(30px, -30px, 0)}60%{-webkit-transform:translate3d(-30px, 30px, 0);transform:translate3d(-30px, 30px, 0)}80%{-webkit-transform:translate3d(30px, 30px, 0);transform:translate3d(30px, 30px, 0)}100%{-webkit-transform:translate3d(0, 0, 0);transform:translate3d(0, 0, 0)}}@media only screen and (max-width: 768px){.index-page header .header-container{padding-right:0}.index-page .banner{float:none;width:100%;height:auto}.index-page .banner img{margin-top:60px}.index-page .introduce{position:fixed;left:0;bottom:0;width:100%;height:auto;padding:0 24px;text-align:center}.index-page .introduce .title{display:none}.index-page .introduce .description{font-size:18px}.index-page .introduce .navigation{display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;margin:30px 0}.index-page .introduce .navigation button{font-size:16px}}